import { UPDATE_STOCK } from '../actions/types'

const initialState = {
    lastStocks: [],
    currentStocks: []
}

export default (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_STOCK:
            return {
                ...state,
                lastStocks: state.currentStocks,
                currentStocks: action.stocks,
            };
        default:
            return state;
    }
}